<template lang="html">
  <div class="form-input-box">
    <div class="form-input__label" :style="{'width': width, 'color': labelColor}">{{label}}</div>
    <div class="form-input__input">
      <input v-if="type === 'text'" class="input" type="text" :disabled="disabled" v-model="currValue" @keyup="$emit('keyup', $event)"  @input="$emit('input', currValue)" @change="$emit('change', currValue)"
      :style="{'color': color, 'fontSize': fontS, 'textAlign': textAlign}" :placeholder="placeholder">
      <input v-if="type === 'password'" class="input" type="password" :disabled="disabled" v-model="currValue" @keyup="$emit('keyup', $event)"  @input="$emit('input', currValue)" @change="$emit('change', currValue)"
      :style="{'color': color, 'fontSize': fontS, 'textAlign': textAlign}" :placeholder="placeholder">
    </div>
  </div>
</template>

<script>
export default {
  name: 'flmFormInput',
  data () {
    return {
      currValue: this.value
    }
  },
  watch: {
   value (val) {
     this.currValue = val;
   },
   currValue (val) {
     this.$emit('input', val);
   }
 },
  props: {
    type: {
      type: String,
      default: 'text'
    },
    disabled: {
      type: Boolean,
      default: false
    },
    value: {},
    width: {
      type: String,
      default: '1.8rem'
    },
    label: {
      type: String,
      required: true
    },
    labelColor: {
      type: String,
      default: '#333'
    },
    color: {
      type: String,
      default: '#666'
    },
    fontS: {
      type: String,
      default: '.28rem'
    },
    textAlign: {
      type: String,
      default: 'left'
    },
    placeholder: {
      type: String,
      default: '请输入'
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../../assets/css/variable.less';
@import '../../../assets/css/property.less';
.form-input-box {
  overflow: hidden;
  font-size: .3rem;
  color: @defaultColor;
  background-color: @whiteColor;
  height: .88rem;
  line-height: .88rem;
  .form-input__label {
    float: left;
    text-align: left;
  }
  .form-input__input {
    overflow: hidden;
    .input {
      font-size: .32rem;
      width: 100%;
      height: .8rem;
      border: none;
      color: @midleColor;
      &::placeholder {
        color: @lightColor;
      }
    }
    .input:disabled {
      background-color: @whiteColor;
    }
  }
}
</style>



