import Loading from './loading'
import Star from './star'
import formInput from './formInput'


export default {
  install (Vue, opts = {}) {
    Vue.component(Star.name, Star)
    Vue.component(formInput.name, formInput)
    
    Vue.prototype.$loading  = Loading
  },
  Star
}



