<template lang="html">
  <div class="mark-load animated slideInLeft">
    <div class="mark-load__icon">
      <i class="fa fa-spinner fa-pulse fa-4x fa-fw"></i>
      <div class="mark-load__msg">{{message || '拼命加载中...'}}</div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    message: ''
  }
}
</script>

<style lang="less">
@bg_gray: rgba(255,255,255,0.5);
@font_blue: #388bff;
.mark-load {
  position: fixed;
  height: 100%;
  width: 100%;
  background: @bg_gray;
  top: 0;
  left: 0;
  .mark-load__icon {
    color:@font_blue;
    position: absolute;
    left:50%;
    top:50%;
    text-align: center;
    transform: translate(-50%,-50%);
  }
  .mark-load__msg {
    color: #666;
    font-size: 1.3rem;
    text-align: center;
    margin-top: 0.8rem;
  }
}
</style>



