/**
 *   对Date的扩展，将 Date 转化为指定格式的String
 *   月(M)、日(d)、小时(h)、分(m)、秒(s)、季度(q) 可以用 1-2 个占位符，
 *   年(y)可以用 1-4 个占位符，毫秒(S)只能用 1 个占位符(是 1-3 位的数字)
 *   例子：
 *   (new Date()).Format('yyyy-MM-dd hh:mm:ss.S') ==> 2006-07-02 08:09:04.423
 *   (new Date()).Format('yyyy-M-d h:m:s.S')      ==> 2006-7-2 8:9:4.18
 */
const fmtDate = (date, fmt) => { // author: meizz
    var o = {
        'M+': date.getMonth() + 1, // 月份
        'd+': date.getDate(), // 日
        'h+': date.getHours(), // 小时
        'm+': date.getMinutes(), // 分
        's+': date.getSeconds(), // 秒
        'q+': Math.floor((date.getMonth() + 3) / 3), // 季度
        'S': date.getMilliseconds() // 毫秒
    };
    if (/(y+)/.test(fmt)) {
        fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
    }
    for (var k in o) {
        if (new RegExp('(' + k + ')').test(fmt)) {
            fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length)))
        }
    }
    return fmt
}

/**
 * 配置节流函数, 用法 var lazyLayout = throttle(calculateLayout, 300); $(window).resize(lazyLayout)
 * @param  {[Function]}  fn     [要执行的函数]
 * @param  {[Number]}  delay    [延迟执行的毫秒数]
 * @param  {[Number]}  mustRun  [至少多久执行一次]
 * @return {[Function]}         [节流函数]
 */
const throttle = (fn, wait, mustRun) => {
    let timeout;
    let startTime = new Date();
    return function() {
        let context = this;
        let args = arguments;
        let curTime = new Date();

        clearTimeout(timeout);
        // 如果达到了规定的触发时间间隔，触发 handler
        if (curTime - startTime >= mustRun) {
            fn.apply(context, args);
            startTime = curTime;
            // 没达到触发间隔，重新设定定时器
        } else {
            timeout = setTimeout(fn, wait);
        }
    };
}

/**
 * 判断是否为对象
 * @param  {[Object]} obj [要判断的对象]
 * @return {[Boolen]}     [返回结果]
 */
const isObject = function(obj) {
  return obj !== null && typeof obj === 'object';
}

/**
 * 判断元素是否拥有某个class
 * @param  {[Object]}  el  [节点元素]
 * @param  {[string]}  cls [class 名称]
 * @return {Boolean}     [description]
 */
const hasClass = (el, cls) => {
  if (!el || !cls) return false;
  if (cls.indexOf(' ') !== -1) throw new Error('className should not contain space.');
  if (el.classList) {
    return el.classList.contains(cls);
  } else {
    return (' ' + el.className + ' ').indexOf(' ' + cls + ' ') > -1;
  }
};

/**
 * 为某个元素添加class
 * @param {[Object]} el  [节点元素]
 * @param {[string]} cls [class 名称]
 */
const addClass = (el, cls) => {
  if (!el) return;
  var curClass = el.className;
  var classes = (cls || '').split(' ');

  for (var i = 0, j = classes.length; i < j; i++) {
    var clsName = classes[i];
    if (!clsName) continue;

    if (el.classList) {
      el.classList.add(clsName);
    } else {
      if (!hasClass(el, clsName)) {
        curClass += ' ' + clsName;
      }
    }
  }
  if (!el.classList) {
    el.className = curClass;
  }
};

/**
 * 移除元素上的class
 * @param  {[Object]} el  [节点元素]
 * @param  {[string]} cls [class 名称]
 * @return {[type]}     [description]
 */
const removeClass = (el, cls) => {
  if (!el || !cls) return;
  var classes = cls.split(' ');
  var curClass = ' ' + el.className + ' ';

  for (var i = 0, j = classes.length; i < j; i++) {
    var clsName = classes[i];
    if (!clsName) continue;

    if (el.classList) {
      el.classList.remove(clsName);
    } else {
      if (hasClass(el, clsName)) {
        curClass = curClass.replace(' ' + clsName + ' ', ' ');
      }
    }
  }
  if (!el.classList) {
    el.className = trim(curClass);
  }
};

/**
 * 获取当前位置, 此方法为异步，通过callback设置相关值
 * @return {[String]} []
 */
const getCurrentLocation = (callback) => {
	var geolocation = new BMap.Geolocation()
  var geoc = new BMap.Geocoder()  // 你地址解析对象

	geolocation.getCurrentPosition(function(result){
		if(this.getStatus() == BMAP_STATUS_SUCCESS){
			var point = result.point
			geoc.getLocation(point, function(rs){
        var addComp = rs.addressComponents
        // 返回一个当前地理信息对象
        var data = {...addComp, ...point}
        // 执行回调
        callback(data)
      })
		}
		else {
			alert('failed'+this.getStatus());
		}
	},{enableHighAccuracy: true})
}
