/**
 * 获取当前定位显示显示相关地理信息地图
 * @param  {Function} callback [description]
 * @return {[type]}            [description]
 */
/**
 * [initCurrentMap description]
 * @param  {String} [idBox='']          [地图容器]
 * @param  {[type]} [callback=null]     [回调返回map实例]
 * @param  {[type]} [point=null]        [point.lng 纬度, point.lat: 经度]
 * @param  {[type]} [dragendHandle=null } = {}] [拖拽回调]
 * @return {[type]}                     [description]
 */
const initCurrentMap = ({ idBox = '', callback = null, point = null, dragendHandle = null } = {}) => {
  // 获取当前定位
  var map = new BMap.Map(idBox)
  if (point && typeof point === 'object') {
    var point = new BMap.Point(point.lng, point.lat)
    getPointMap({map, point, callback, dragendHandle})
  } else {
    var geolocation = new BMap.Geolocation()
    geolocation.getCurrentPosition(function (r) {
      if (this.getStatus() === BMAP_STATUS_SUCCESS) {
        getPointMap({ map, point: r.point, callback, dragendHandle })
      } else {
        console.log('failed' + this.getStatus())
      }
    }, {enableHighAccuracy: true})
  }
}

/**
 * 根据经纬度标记覆盖物以及拖放
 * @param  {[type]}   map           [description]
 * @param  {[type]}   point         [description]
 * @param  {Function} callback      [description]
 * @param  {[type]}   dragendHandle [description]
 * @return {[type]}                 [description]
 */
function getPointMap ({ map = {}, point = null, callback = null, dragendHandle = null } = {}) {
  var mk = new BMap.Marker(point)
  map.centerAndZoom(point, 18)
  map.addOverlay(mk)
  map.panTo(point)
  map.enableScrollWheelZoom(true)

  var Cgeoc = new BMap.Geocoder()
  Cgeoc.getLocation(point, function(rs){
    let addComp = rs.addressComponents;
    // dragendHandle(addComp)
    typeof callback === 'function' && callback(map,
      {point, title:'', address: addComp.city + addComp.district + addComp.street + addComp.streetNumber, ...addComp})
  })

  if (typeof dragendHandle === 'function') {
    mk.enableDragging()
    mk.addEventListener("dragend", function(e){
      var geoc = new BMap.Geocoder()
      geoc.getLocation(e.point, function(rs){
        var addComp = rs.addressComponents;
        dragendHandle(addComp)
      })
    })
  }
}

const searchComplete = (map, val, addArr, callback) => {
  var options = {
    onSearchComplete: function (results) {
      if (local.getStatus() === BMAP_STATUS_SUCCESS) {
        var geoc = new BMap.Geocoder()
        for (let i = 0; i < results.getCurrentNumPois(); i++) {
          (function(i){
            var data = results.getPoi(i)
            var point = new BMap.Point(data.point.lng, data.point.lat)
            geoc.getLocation(point, function(rs){
              var addComp = rs.addressComponents
              data.district = addComp.district
              addArr.push(data)
              typeof callback === 'function' && callback()
            })
          })(i)
        }
      }
    }
  }
  var local = new BMap.LocalSearch(map, options)
  local.search(val)
}


/**
 * 上传文件方法
 * @param  {[type]} e [input file change 的事件对象]
 * @return {[type]}   [description]
 */
const uploadPic = (files, callback, isCompress = false, size = 1) => {
  const maxsize = size * 1024 * 1024;
  var dataArr = []
  minutils.IndicatorOpen('上传中...')

  for (var i = 0; i < files.length; i++) {
    (function (i){
      var file = files[i]
      console.log(file)
      // 接受 jpeg, jpg, png 类型的图片
      if (!/\/(?:jpeg|jpg|png|gif|bmp)/i.test(file.type)) return;

      let quality = maxsize / file.size
      lrz(file, { quality: quality >= 1 ? 0.7 : quality }).then(res => {
        let index = res.base64.indexOf(',')
        dataArr.push({imgData: res.base64, base64Img: res.base64.substr(index + 1), fileName: file.name})
        if (i === (files.length -1)) {
          minutils.IndicatorClose()
          callback(dataArr)
        }
      })
    })(i)
  }
}

const isLogin = () => {
  // return (JSON.parse(sessionStorage.getItem('userInfo')) || {}).id
  var data = (sessionStorage.getItem('curUser') && JSON.parse(base64.decode(sessionStorage.getItem('curUser')))) || (sessionStorage.getItem('curUser') && JSON.parse(base64.decode(localStorage.getItem('curUser'))))
  if (data) {
    return !!data
  } else {
    return false
  }
}

const hasUser = (noLogin) => {
  var curUser = (sessionStorage.getItem('curUser') && JSON.parse(base64.decode(sessionStorage.getItem('curUser')))) || (sessionStorage.getItem('curUser') && JSON.parse(base64.decode(localStorage.getItem('curUser'))))
  if (curUser) {
    return curUser
  } else {
    typeof noLogin === 'function' && noLogin()
  }
}

const userId = () => {
  var curUser = (sessionStorage.getItem('curUser') && JSON.parse(base64.decode(sessionStorage.getItem('curUser')))) || (sessionStorage.getItem('curUser') && JSON.parse(base64.decode(localStorage.getItem('curUser'))))
  if (curUser) {
    return curUser.user ? curUser.user.userId : curUser.enterprise.userId
  } else {
    return null
  }
}

const getType = () => {
  var curUser = (sessionStorage.getItem('curUser') && JSON.parse(base64.decode(sessionStorage.getItem('curUser')))) || (sessionStorage.getItem('curUser') && JSON.parse(base64.decode(localStorage.getItem('curUser'))))
  if (curUser) {
    return curUser.type
  } else {
    return null
  }
}

const getUser = () => {
  var curUser = (sessionStorage.getItem('curUser') && JSON.parse(base64.decode(sessionStorage.getItem('curUser')))) || (sessionStorage.getItem('curUser') && JSON.parse(base64.decode(localStorage.getItem('curUser'))))
  if (curUser) {
    return curUser.user || curUser.enterprise
  } else {
    return null
  }
}

export default {
  fmtDate,
  throttle,
  isObject,
  hasClass,
  addClass,
  removeClass,
  getCurrentLocation,
  initCurrentMap,
  searchComplete,
  uploadPic,
  isLogin,
  hasUser,
  userId,
  getType,
  getUser,
  getPathOfBus,
  drawPath
}



