// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import './utils/flexible'
import Vue from 'vue'
// 引入中文库用于验证
import zh_CN from './utils/zh_CN'
import VeeValidate, { Validator } from 'vee-validate'
import App from './App'
import store from './store'
import router from './router'
import flmPlugin from './plugin'
import filters from './utils/filters'
// min 不大引用太多不作区分
import Mint from 'mint-ui'
import 'mint-ui/lib/style.css'
import 'normalize.css'
import 'animate.css'
import './assets/css/reset.css'
import './assets/css/index.less'
import 'font-awesome/css/font-awesome.css'

import minutils from '@/utils/minUtils'

Vue.use(Mint)
Vue.use(flmPlugin)
// 配置验证插件
Validator.addLocale(zh_CN)
Vue.use(VeeValidate, { locale: 'zh_CN' })

Vue.config.productionTip = false

// 实例化Vue的filter。
Object.keys(filters).forEach(k => Vue.filter(k, filters[k]))

// 路由拦截功能,判断用户是否登录
router.beforeEach((to, from, next) => {
  // 关闭加载
  minutils.IndicatorClose()
  // 未登录
  if (to.matched.some(record => record.meta.reqLogin) && !sessionStorage.getItem('curUser') && !localStorage.getItem('curUser')) {
    next({ path: '/login' })
  } else if (to.path === '/developfunction') {
    minutils.Toast({
      message: '功能正在升级, 敬请期待',
      iconClass: 'u-icon u-icon-dev'
    })
    next(false)
  } else {
    next()
  }
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App }
})



