import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

const Store = new Vuex.Store({
  state: {
    // 保存当前位置状态
    location: {},
    searchItem: {}
  },
  getters: {
    getLocation (state) {
      return state.location
    },
    getSearchItem (state) {
      return state.searchItem
    }
  },
  mutations: {
    setLocation (state, data) {
      state.location = {...data}
    },
    setSearchItem (state, data) {
      state.searchItem = {...data}
    }
  },
  actions: {
    setLocation (context, data) {
      context.commit('setLocation', data)
    },
    setSearchItem (context, data) {
      context.commit('setSearchItem', data)
    }
  }
})

export default Store



