/**
 * 提供对 minui的再封装
 */
import { Toast, Indicator, MessageBox } from 'mint-ui'
const minUtils = {
  // 消息提示弹窗
  Toast (options) {
    Toast(options)
  },
  // 打开加载动画
  IndicatorOpen (options) {
    if (typeof options === 'string') {
      options = { text: options, spinnerType: 'fading-circle' }
    }
    Indicator.open(options)
  },
  // 关闭加载动画
  IndicatorClose () {
    Indicator.close()
  },
  // alert弹窗 谷歌会报 Uncaught (in promise) cancel 属于正常现象
  MessageBox ({message = ' ', title = '提示', callback = '', options = {}} = {}) {
    let msg = MessageBox.alert(message, title, options).then(action => {
      typeof callback === 'function' && callback(action)
    })
    document.querySelector('.mint-msgbox-confirm').style.color = '#2cc482'
  },
  // confirm弹窗 谷歌会报 Uncaught (in promise) cancel 属于正常现象
  MessageBoxConfirm ({message = ' ', title = '提示', success = null, fail = null, options = {}} = {}) {
    MessageBox.confirm(message, title, options).then(action => {
      typeof success === 'function' && success(action)
    }, action => {
      typeof fail === 'function' && fail(action)
    })
    document.querySelector('.mint-msgbox-confirm').style.color = '#2cc482'
  },
  // confirm弹窗 谷歌会报 Uncaught (in promise) cancel 属于正常现象
  MessageBoxPrompt ({message = ' ', title = '提示', success = null, fail = null, options = {}} = {}) {
    MessageBox.prompt(message, title, options).then(({ value, action }) => {
      typeof success === 'function' && success(value, action)
    }, action => {
      typeof fail === 'function' && fail(action)
    })
    document.querySelector('.mint-msgbox-confirm').style.color = '#2cc482'
  }
}

export default minUtils



