import utils from './utils'

/**
 * 格式日期时间过滤器
 */
const toFormatDate = (dateTime, sFormat) => {
  return utils.fmtDate(new Date(dateTime), sFormat)
}

/**
 * 格式手机号码过滤器
 */
const toFormatMobile = (mobile) => {
  if (!mobile) return
  var str1 = mobile.substring(0, 3)
  var str2 = mobile.substring(3, 7)
  var str3 = mobile.substring(7, 11)
  return str1 + ' ' + str2 + ' ' + str3
}
/**
 * 格式距离（m -> km）
 */
const toFormatDistance = (num) => {
  if (!num) {
    return ''
  } else if (parseInt(num) < 1000) {
    return parseInt(num) + 'm'
  } else if (parseInt(num) >= 1000) {
    return (parseInt(num) / 1000).toFixed(2) + 'km'
  }
}

/**
 * 过滤姓名
 */
const toFormatName = (name) => {
  if (!name) return
  return name.substring(0, 1)
}
/**
 * 过滤分钟 2分20秒
 */
const toFormatMinute = (time) => {
  let second = time % 60
  let minute = parseInt( time / 60 ) % 60
  let hour = parseInt( parseInt( time / 60 ) / 60 )
  if (hour === 0) return `${minute}分${second}秒`
  return `${hour}小时${minute}分${second}秒`
  // return name.substring(0, 1)
}

export default {
  toFormatDate,
  toFormatName,
  toFormatMobile,
  toFormatMinute,
  toFormatDistance
}



