<template lang="html">
  <div class="star-block">
    <span @click="selStar(1)" class="star"><i :class="{'active': amount > 0.5}"></i></span>
    <span @click="selStar(2)" class="star"><i :class="{'active': amount > 1.5}"></i></span>
    <span @click="selStar(3)" class="star"><i :class="{'active': amount > 2.5}"></i></span>
    <span @click="selStar(4)" class="star"><i :class="{'active': amount > 3.5}"></i></span>
    <span @click="selStar(5)" class="star"><i :class="{'active': amount > 4.5}"></i></span>
  </div>
</template>

<script>
export default {
  name: 'FlmStar',
  props: {
    readonly: {
      type: Boolean,
      default: false
    },
    starNum: {
      type: String | Number,
      default: '0'
    }
  },
  data () {
    return {
      amount: this.starNum
    }
  },
  methods: {
    selStar (num) {
      if (this.readonly) return
      this.amount = num
      this.$emit('star-change', num, this)
    }
  }
}
</script>

<style lang="less" scoped>
  .star-block {
    .star {
      display: inline-block;
      width: .45rem;
      height: .42rem;
      margin-right: .12rem;
      background: url('../../../assets/img/star02@3x.png') no-repeat top left;
      background-size: cover;
      i.active {
        display: inline-block;
        width: .45rem;
        height: .42rem;
        background: url('../../../assets/img/star@3x.png') no-repeat top left;
        background-size: cover;
      }
    }
  }
</style>



